.pages-container {
  display:flex;
  width: 73%;
}

.criteria-head {
  font-family: Georgia, 'Times New Roman', Times, serif;
  color: rgb(95, 88, 88);
    margin-bottom: 20px;
    font-size: 30px;
    margin-left: 0.5px;
    margin-top: 0.5px;

}
.text-area-field {
  width: 300px;
  height: 100px;
}

.left-sides, .righht-sides {
  width: 50%; /* Set width to 48% to accommodate for spacing */
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.left-sides{
  background-color: #f0f0f0;
  flex: 1;
  height: 650px;
}


/* Style for form elements */
.left-sides form {
  margin-top: 20px;
}

.left-sides input[type="text"] {
  width: calc(100% - 10px);
  padding: 8px;
  margin-bottom: 10px;
}

.left-sides button {
  padding: 10px 20px;
  background-color: #000000;
  color: white;
  border: none;
  cursor: pointer;
}

.left-sides button:hover {
  background-color: #0056b3;
}

.righht-sides {
  flex: 1;
  height: 650px;
  width: 50%; /* Adjust the width to make the right side smaller */
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 2px;
  background-color: #e0e0e0;
  margin-right: 10px; /* Add margin-right to shift the right side to the left */
  margin-bottom: 20px;
  overflow-y: auto; /* Make only the y-axis scrollable */
  overflow-x: hidden; /* Hide horizontal scrollbar */
}

.righht-sides .fees-group-list table {
  width: 100%;
  border-collapse: collapse;
}

.righht-sides .fees-group-list th,
.fees-group-list td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.righht-sides .fees-group-list th {
  background-color: #b1abab;
  color: #1a0707;
}

.fees-group-list th {
  background-color: #f2f2f2;
}

.fees-group-list tr:nth-child(even) {
  background-color: #f2f2f2;
}

.fees-group-list tr:hover {
  background-color: #ddd;
}

hr {
  margin: 20px 0;
  border: none;
  border-top: 1px solid #ccc;
}

.sec button {
  top: 10px;
  width: 8rem;
  height: 40px;
  background-color: rgb(3, 3, 0);
  margin-top: 0.5rem;
  font-size: 18px;
}