.split-container {
  display: flex;
  
}

.left-side {
  flex: 1;
  margin-right: 10px;
  margin-bottom: 20px;
}

.rightss-side {
  flex: 1;
  height: 650px;
  width: 40%; /* Adjust the width to make the right side smaller */
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 2px;
  background-color: #e0e0e0;
  margin-right: 10px; /* Add margin-right to shift the right side to the left */
  margin-bottom: 20px;
  overflow-y: auto; /* Make only the y-axis scrollable */
  overflow-x: hidden; /* Hide horizontal scrollbar */
}

.select-field,
.input-field,
.dueDate {
  width: 60%;
  padding: 8px;
  margin-top: 5px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.label {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.label input[type="radio"] {
  margin-right: 40px;
}

.button {
  margin: 10px;
  padding: 8px 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}


.button:hover {
  background-color: #0056b3;
}

.card-list {
  margin-top: 20px;
}

.rightss-side .card-list table {
  width: 85%;
  border-collapse: collapse;
}

.rightss-side .card-list th,
.card-list td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.rightss-side .card-list th {
  background-color: #b1abab;
  color: #1a0707;
}

.card-list th {
  background-color: #f2f2f2;
}

.card-list tr:nth-child(even) {
  background-color: #f2f2f2;
}

.card-list tr:hover {
  background-color: #ddd;
}

hr {
  margin: 20px 0;
  border: none;
  border-top: 1px solid #ccc;
}

.criteria-heading {
  font-family: Georgia, 'Times New Roman', Times, serif;
  color: rgb(95, 88, 88);
  margin-bottom: 20px;
  font-size: 30px;
  margin-left: 10px;
  margin-top: 10px;
}
