/* studentAdmissionForm.css */
.form-container {
  width: 100%;
  margin: auto;
}

.form-row {
  display: flex;
  align-items: center; /* Center align items vertically */
  margin-bottom: 20px;
}

.form-field {
  flex: 1;
  margin-right: 10px;
  padding-left: 0; /* Remove left padding */
}

.label {
  display: inline-block;
  width: 150px; /* Adjust width as needed */
  font-weight: bold;
}

input[type="text"],
input[type="number"],
input[type="date"],
select,
textarea {
  width: 100%; /* Make input fields full width */
  padding: 8px;
  margin: 5px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 16px; /* Adjust font size as needed */
}

.stepper-container {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.stepper-button-container {
  display: flex;
  justify-content: space-evenly;
  padding: auto;
}

.stepper-button-container button {
  margin-top: 10px;
}

.required::after {
  content: "*";
  color: red;
}

.size {
  height: 20px;
}
