body{
  overflow-x: hidden;
}

.split-container {
  display:flex;
  width: 77%;
}

.criteria-heading {
  font-family: Georgia, 'Times New Roman', Times, serif;
  color: rgb(95, 88, 88);
  margin-bottom: 20px;
  font-size: 30px;
  margin-left: 10px;
  margin-top: 10px;

}

.text-area-field {
  width: 300px;
  height: 100px;
}

.left-side, .rightt-side {
  width: 60%; /* Set width to 48% to accommodate for spacing */
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.left-side {
  background-color: #f0f0f0;
  flex: 1;
}

.rightt-side {
  background-color: #e0e0e0;
  flex: 0.5; /* Adjust the flex value to change the size */
}

/* Style for form elements */
.left-side form {
  margin-top: 20px;
}

.left-side input[type="text"] {
  width: calc(100% - 10px);
  padding: 8px;
  margin-bottom: 10px;
}

.left-side button {
  padding: 10px 20px;
  background-color: #000000;
  color: white;
  border: none;
  cursor: pointer;
}

.left-side button:hover {
  background-color: #0056b3;
}

.rightt-side input[type="text"] {
  width: calc(100% - 10px);
  padding: 8px;
  margin-bottom: 10px;
}

.rightt-side .card-list table {
  width: 100%;
  border-collapse: collapse;
}

.rightt-side .card-list th,
.card-list td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.rightt-side .card-list th {
  background-color: #b1abab;
  color: #1a0707;
}

.rightt-side {
  flex: 1;
  height: 650px;
  width: 50%; /* Adjust the width to make the right side smaller */
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 2px;
  background-color: #e0e0e0;
  margin-right: 10px; /* Add margin-right to shift the right side to the left */
  margin-bottom: 20px;
  overflow-y: auto; /* Make only the y-axis scrollable */
  overflow-x: hidden; /* Hide horizontal scrollbar */
  
}

.search-input {
  width: 70%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

.button {
  top: 10px;
  width: 8rem;
  height: 40px;
  background-color: rgb(93, 93, 87);
  margin-top: 0.5rem;
  font-size: 18px;
}
 

 
